import React, { useRef, useState } from "react"
import { useStaticQuery, graphql, Link } from "gatsby"
import Layout from "../components/layout"
import Seo from "../components/seo"
import BannerWorks from "../components/banner/works"
import FreeConsultationForm from "../components/free-consultation-form"
import WorkCard from "../components/work/work-card"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faChevronDown, faChevronUp } from '@fortawesome/free-solid-svg-icons'
import { useTranslation } from "gatsby-plugin-react-i18next"

const WorksPage = ({ data }) => {
  const [activeNav, setActiveNav] = useState('all')
  const [isNavVisible, setIsNavVisible] = useState(false)
  const navRef = useRef(null)
  const { t } = useTranslation()

  const { allStrapiService, allStrapiWork, strapiGlobal } = data

  const seo = {
    metaTitle: t('our works'),
  }

  const navItems = [
    {
      title: t('all'),
      slug: 'all',
    },
    ...allStrapiService.nodes,
  ];

  const navItemClasses = [
    'bg-concrete',
    'border-b-[2px]',
    'border-secondary/10',
    'md:border-transparent',
    'py-2',
    'text-2xl',
    'md:hover:text-primary',
    'md:hover:border-primary',
    'md:hover:border-b-[1px]',
    'md:font-bold',
    'md:bg-transparent',
  ];

  const activeNavItemClasses = [
    'md:text-primary',
    '!border-b-[1px]',
    'font-bold',
    'md:font-regular',
  ]

  const navWrapperClasses = [
    'flex',
    'flex-wrap',
    'flex-col',
    'md:flex-row',
    'justify-center',
    'md:space-x-8',
    'overflow-hidden',
    'md:overflow-auto',
    'md:h-auto',
    'transition transition-all',
  ];
  
  // TODO - Remove on prod
  const workArray = [...Array(6).keys()].map(() => allStrapiWork.nodes[0]);
  let filteredResults = workArray;

  if (activeNav !== 'all') {
    filteredResults = workArray.filter(({ services }) => services.some(({ slug }) => slug === activeNav));
  }

  return (
    <Layout>
      <Seo seo={seo} />
      <BannerWorks />
      <main>
        <div
          ref={navRef}
          className="w-screen md:w-full md:container flex flex-wrap flex-col"
        >
          <button
            className={[
              'font-bold',
              'md:hidden',
              ...navItemClasses
            ].join(' ')}
            onClick={() => setIsNavVisible(!isNavVisible)}
          >
            portfolio categories
            &nbsp;
            <FontAwesomeIcon icon={faChevronDown} />
          </button>
          <div className={[
            isNavVisible ? 'h-auto' : 'h-0',
            ...navWrapperClasses,
          ].join(' ')}>
            { navItems.map(({
              id,
              title,
              slug
            }) => (
              <button
                key={`service-nav-${id}`}
                className={[
                  ...navItemClasses,
                  ...(activeNav === slug ? activeNavItemClasses : []),
                ].join(' ')}
                onClick={() => setActiveNav(slug)}
              >
                {title}
              </button>
            ))}
          </div>
        </div>
        <section className="container">
          <div className="py-14">
            { filteredResults.map((work, index) => (
              <WorkCard
                key={`work-test-${work.id}-${index}`}
                odd={index % 2}
                {...work}
              />
            )) }
          </div>
          <div className="py-14 text-center">
            <button className="button-main" onClick={() => {
              // @ts-ignore-next-line
              navRef.current.scrollIntoView({ behavior: "smooth" })
            }}>
              {t('view other works')}
              &nbsp;
              <FontAwesomeIcon icon={faChevronUp} />
            </button>
          </div>
        </section>
        <FreeConsultationForm {...{ contactDetails: strapiGlobal.contactDetails }} />
      </main>
    </Layout>
  )
}

export const query = graphql`
  query(
    $language: String!
    $strapiLangKey: String!
  ) {
    locales: allLocale(filter: {language: {eq: $language}}) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
    strapiGlobal(locale: {eq: $strapiLangKey}) {
      ...ContactDetails
    }
    allStrapiService(
      filter: {
        locale: {
          eq: $strapiLangKey
        }
        slug: {
          nin: "sem",
        }
      }
    ) {
      nodes {
        id
        slug
        title
      }
    }
    allStrapiWork(filter: {
      locale: {
        eq: $strapiLangKey
      }
    }) {
      nodes {
        id
        title
        excerpt {
          data {
            excerpt
          }
        }
        services {
          id
          slug
          title
        }
        featureImage {
          alternativeText
          ... on STRAPI__MEDIA {
            localFile {
              childImageSharp {
                gatsbyImageData(
                  width: 788
                  placeholder: BLURRED
                )
              }
            }
          }
        }
        link {
          id
          type
          url
        }
        slug
      }
    }
  }
`

export default WorksPage
